
ul,
li {
    list-style: none;
}

a,
a:visited,
a:hover {
    color: inherit;
    text-decoration: none;
}

header {
    max-width: 1600px;

    height: 100%;
    
    font-family: 'Roboto',
    sans-serif;
    font-style: normal;
    font-weight: 400;
    
    letter-spacing: 0.03em;
    font-size: 14px;
    line-height: 1.14;
    background-color: grey;
    color: $headerTextColor;


    background-image: linear-gradient(rgba(47, 48, 58, 0.4),
            rgba(47, 48, 58, 0.4)),
        url("../images/headerPage1/mobilex1.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;


    @media screen and (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                rgba(47, 48, 58, 0.4)),
            url("../images/headerPage1/mobilex2.jpg");
    }

    @media screen and (min-width: 768px) {
        background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                rgba(47, 48, 58, 0.4)),
            url("../images/headerPage1/tablex1.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        max-width: 768px;

        @media screen and (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                    rgba(47, 48, 58, 0.4)),
                url("../images/headerPage1/tablex2.jpg");
        }
    }

    @media screen and (min-width: 1200px) {
        padding-top: 200px;
        padding-bottom: 200px;

        background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                rgba(47, 48, 58, 0.4)),
            url("../images/headerPage1/desktopx1.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        max-width: 1600px;
        height: 600px;

        @media screen and (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: linear-gradient(rgba(47, 48, 58, 0.4),
                    rgba(47, 48, 58, 0.4)),
                url("../images/headerPage1/desktopx2.jpg");
        }
    }
}

.header-wrapper {
    padding: 45px 32px 0 32px;

@media screen and (min-width: 320px) and (max-width: 767px) {
    
        padding: 45px 20px 0 20px;
    
}
}



.header-top {
    display: flex;
    align-items: center;

    margin-bottom: 53px;
}

.logo-link {
    font-weight: 500;
    font-size: 30px;
    line-height: 1.16;

    display: flex;
    justify-content: center;
    align-items: center;


}



.logo-images {
    margin-right: 10px;

    @media screen and (min-width: 300px) and (max-width:767px) {
        
            display: none;
        
    }
}


.nav-menu {
    margin-left: auto;
}

.list-nav {
    display: flex;

    font-weight: 500;
    font-size: 12px;
    line-height: 1.67px;

    text-transform: uppercase;
}


.item {
    display: block;

    &:not(:last-child) {
        margin-right: 39px;
    }
}

.item-current {

    border-bottom: 3px solid $pageNumberBackgroundColor;

}

.header-form {

    display: flex;
    justify-content: center;
    padding-bottom: 80px;

    @media screen and (min-width: 320px) and (max-width: 767px) {
        
            padding-bottom: 92px;
        
    }
}



.header-input {


    border: none;
    width: 336px;
    border-bottom: 0.5px solid $headerTextColor;

    background: transparent;

    padding: 4px;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        outline: none;
        border-bottom: 1px solid $pageNumberBackgroundColor;
        color: $headerTextColor
    }

    &::placeholder {
        color: $headerTextColor;
    
    }
}


.header-button--submit {
    padding: 0;

    position: relative;
    left: -15px;

    border: none;
    background-color: transparent;

    cursor: pointer;
}

.input-images {
    stroke: #FFFFFF;
    fill: transparent;
}

